import React, { useEffect } from "react";
import { NavLink } from "react-router-dom";
import { connect } from "react-redux";

import "./Header.scss";
import { isEmpty } from "../utils";
import { fetchMenuList } from "../actions";

const Header = (props) => {
    const { menuList, fetchMenuList } = props;

    useEffect(() => {
        fetchMenuList();
    }, [fetchMenuList]);

    const renderList = () =>
        isEmpty(menuList)
            ? null
            : menuList.map((item, index) => (
                  <li className="nav__item" key={`nav-item-${index}`}>
                      <NavLink exact to={item.link} className="nav__link">
                          {item.name}
                      </NavLink>
                  </li>
              ));

    return (
        <header className="section-header">
            <nav className="nav">
                <ul className="nav__list">{renderList()}</ul>
            </nav>
        </header>
    );
};

const mapStateToProps = (state) => ({ menuList: state.menuList });

export default connect(mapStateToProps, { fetchMenuList })(Header);
